@mixin ab($l, $r) {   
    @if $l != n { right: $l; }
    @if $r != n { left: $r; }
}
@mixin fixed($l, $r) {
    @if $l != n { right: $l; }
    @if $r != n { left: $r; }   
}
@mixin rotate($rotate) { 
  transform: rotate(($rotate) * (-1));
}
@mixin border-radius($tr, $tl, $bl, $br) {
  border-radius: $tl $tr $br $bl;
}
@mixin padding($pt, $pr, $pb, $pl) {
  padding: $pt $pl $pb $pr;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin left($l) {
  right: $l;
}
@mixin right($r) {
  left: $r;
}

body[dir="rtl"] {
	direction: rtl;
	.l0:not(.r0) {
		left: auto;
		right: 0;
	}
	.r0:not(.l0) {
		right: auto;
		left: 0;
	}
	footer-mobile-navigation {
		.popup-close {
			right: auto;
			left: 16px;
		}
	}
	.header-icons .bubble-count {
		@include ab(auto,-13px);
	}
	.search-dropdown {
		input {
			border-radius: 0 var(--border-radius) var(--border-radius) 0;
		}
		button {
			border-radius: var(--border-radius) 0 0 var(--border-radius);
		}
	}
	.header-navigation .main-navigation {
		>li {
			mega-dropdown>a:before {
				left: auto;
				right: 0;
				transform-origin: left;
			}
			&:hover mega-dropdown>a:not(.btn):before {
				transform-origin: right;
			}
		}
	}
	.slider-controls {
		.slider-prev-button {
			@include left(auto);
			@include right(-10px);
		}
		.slider-next-button {
			@include right(auto);
			@include left(-10px);
		}
	}
	.filter-list label:before {
		@include ab(0,auto);
	}
	.collection-paginate {
		details {
			>div.l0 {
				@include right(0);
			}
		}
	}
	.collection-sort details >.pa {
		@include left(0);
	}
	.noUi-txt-dir-rtl.noUi-horizontal {
		.noUi-handle {
			@include left(0);
			@include right(auto);
		}
		.noUi-handle-upper {
			right: -16px;
		}
	}
	.flipY {
		@include rotate (0);
	}
	product-card form .btn .icon-option {
		@include rotate (180deg);
	}
	@media (min-width: 750px) {
		.section-icon-list {
			.flex>.block:not(:last-child):after {
				@include ab(auto,-10px);
				@include rotate(-90deg);
			}
		}
	}
	.dropdown.l0 {
		@include left(auto);
		@include right(auto);
	}
	.mega-section {
		left: 0;
		right: 0;
	}
	.mobilebar .mobile-icon-list {
		.bubble-count {
			@include left(auto);
			@include right(-10px);
		}
		.header-icon--home {
			svg {
				height: 20px;
			}
		}
	}
	.mobile-navigation-body {
		.has-sub-layer a {
			.expand svg {
				transform: rotate(90deg);
			}
		}
	}
	.mobile-navigation-layers {
		.sub-main-layer {
			@include left(-100%);
			@include right(auto);
			&.active {
				@include left(0);
				@include right(0);
			}
		}
	}
	.mobile-navigation-header .layer-title svg {
		transform: rotate(180deg);
	}
	.mobile-navigation-close {
		@include left(auto);
		@include right(16px);
	}
	.breadcrumb-list {
		li {
			.icon-breadcrumb {
				transform: rotate(180deg);
			}
		}
	}
	.section-featured-collections {
		@media (min-width: 1300px) {
			.slider-controls {
				.slider-prev-button {
					@include left(auto);
					@include right(-25px);
				}
				.slider-next-button {
					@include right(auto);
					@include left(-25px);
				}
			}
		}
	}
	&.templateProduct .main-product-gallery .slider-controls {
		.slider-next-button {
			@include right(auto);
			@include left(10px);
		}
		.slider-prev-button {
			@include left(auto);
			@include right(10px);
		}
	}
	.main-product-info {
		.swt-type-color, .swt-type-image {
			input.disabled+label:before {
				left: auto;
				right: -1px;
			}
			.tooltip:before {
				left: auto;
				right: 50%;
				transform: rotate(-45deg);
			}
		}
		.option-type-select .select-wrapper svg {
			right: auto;
			left: 15px;
		}
	}
	&.templateProduct .section-featured-collections .featured-collections--list {
		.quantity-inner svg {
			right: auto;
			left: 15px;
		}
		@media (min-width: 750px) {
			.card:nth-child(odd) product-card:after {
				right: auto;
				left: 0;
			}
		}
	}

	.bundle-checklist label:before {
		left: auto;
		right: 0;
	}
	.bundle-content .select-variants>svg {
		right: auto;
		left: 10px;
	}
	.section-static-newsletter-popup {
		.block-form {
			input {
				border-radius: 0 var(--border-radius) var(--border-radius) 0;
			}
			button {
				border-radius: var(--border-radius) 0 0 var(--border-radius);
			}
		}
		.countdown .countdown-item:first-child { 
			@include padding-start(0);
		}
		.countdown .countdown-item {
			padding: 0;
			&:not(:first-child) {
				&:before {
					@include padding-start(6px);
					@include padding-end(0);
				}
			}
			&:after {
				@include padding-start(0);
				@include padding-end(4px);
			}
		}
	}
	.popup-gl .btn-popup-close {
		right: auto;
		left: 0;
	}
	.cart-noti-popup {
		cart-recommendation {
			&:before, &:after {
				right: -42px;
				left: 0;
			}
		}
		&.with-recommend .popup-inner {
			overflow-x: hidden;
		}
	}
	&.templateCart .quantity-select .icon-loading {
		left: auto;
		right: 50%; 
	}
	div.cart-sidebar {
		.popup-inner {
			right: auto;
			left: -100%;
		}
		&.active {
			.popup-inner {
				left: 0;
			}
		}
	}
	@media (min-width: 750px) {
		.product__toggle-nav {
			a {
				&:before {
					right: 0;
					left: auto;
					transform-origin: left;
				}
			}
			&.active, &:hover {
				a {
					&:before {
						transform: scaleX(1);
						transform-origin: right;
					}
				}
			}
		}
		.main-products.flex-list-2 {
			>div:nth-child(2n) product-card:before {
				left: auto;
				right: -20px;
			}
			>div:nth-child(odd) product-card:after {
				right: auto;
				left: 0;
			}
		}
	}
}
